
import axios from 'axios'
import md5 from 'md5'
export const getCharacterByBane = async (name: string) => {
  const timeStamp = new Date().getTime()
  const hash = md5(String(timeStamp))
  const res = await axios.get(
    `https://gateway.marvel.com:443/v1/public/characters?nameStartsWith=${name}&apikey=${process.env.NEXT_PUBLIC_MARVEL_PUBLIC_KEY}&hash=${hash}`
  )
  return res
}
